import './App.css';
import React,{useState,useEffect} from "react";
// import Navbar from './components/Navbar';
import "react-toastify/dist/ReactToastify.css";
import {ToastContainer} from 'react-toastify';
import Home from './components/Home';
import AllCourses from './components/Allcourses';
import AddCourse  from './components/AddCourse';
import { Container,Button,InputGroup } from 'reactstrap';
import { Col } from 'reactstrap';
import { Row } from 'reactstrap';
import Header from './components/Header';
import Menus from './components/Menus';
import About from './components/About';
import Contact from './components/Contact';
import EditCourse from './components/EditCo';
import {BrowserRouter as Router,Route} from 'react-router-dom';
import Navbar from './components/Navbar/Navbar';
import ImageUrl from './images/314799.jpg'
import mobileImage from './images/lucas-hein-3tgwzKpxHa4-unsplash.jpg';

function App() {

  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
    const imageUrl = windowWidth >= 650 ? ImageUrl : mobileImage;

    useEffect(() => {
      const handleWindowResize = () => {
          setWindowWidth(window.innerWidth);
      };
      
      window.addEventListener('resize', handleWindowResize);

      return () => {
          window.removeEventListener('resize', handleWindowResize);
      }
  }, []);

return (
    <div className="imgClass">
      <Router>
        <ToastContainer hideProgressBar={true} autoClose={2000} />
        <Navbar/>
        <Container>
        {/* <Header/> */}
        <Route path="/" exact component={Home} />
        <Route path="/add-course" exact component={AddCourse} />
        <Route path="/view-courses" exact component={AllCourses} />
        <Route path="/about" exact component={About} />
        <Route path="/contact" exact component={Contact} />
        </Container>
      </Router>
      
    </div>
  );
}

export default App;
