import axios from 'axios';
import React,{useEffect,useState} from 'react';
import { toast } from 'react-toastify';
import {Jumbotron,Container, Table} from 'reactstrap';
import { CustomDialog, useDialog } from 'react-st-modal';
import {Button} from 'reactstrap';

const Home = () => {

    const [data,setData] = useState([]);
    const [hide,setHide] = useState(false);
    useEffect(() => {
        document.title = "Home"
    },[])

    const btnHandle = () => {
        toast.success("Completed",{
            position:'bottom-center'
        })
    }

    const btnHandle1 = () => {
        axios.get(`/getData`).then(
            (response) => {
                console.log(response.data);
                setData(response.data);
                setHide(false);
            },
            (error) => {
                console.log(error);
            }
        )
    }

    return(
        <div>
            <Jumbotron className="text-center">
                <p className="display-4">Learning Jumbotrons</p>
                <p>This is the page used for description</p>
                <Container>
                    <Button color="primary" onClick={btnHandle}>Start Using</Button>
                </Container>
            </Jumbotron>
            {/* <Button color="primary" onClick={btnHandle1}>Click</Button> */}
            <br></br>
            <br></br>
            <div className="table-responsive w-100">
                <table className="table table-bordered">
                    {
                        data.map((item,i)=>{
                            return(    
                                <tr key={i}>
                                    <td className="text-center">{item.id}</td>
                                    <td>{item.title}</td>
                                    <td>{item.description}</td>
                                    <td>{item.phone}</td>
                                    <td>{item.pincode}</td>
                                    <td>{item.date}</td>
                                </tr>
                            )
                        })
                    }
                </table>
            </div>

            
        
        
        </div>
        
    );
}

export default Home;

