import React, {useState, useEffect} from "react";
import Course from './Course';
import GlitchClip from 'react-glitch-effect/core/GlitchClip';
import  {BeatLoader,CircleLoader,PulseLoader} from 'react-spinners';
import base_url from '../api/bootapi'
import {
    Audio,
    BallTriangle,
    Bars,
    Circles,
    Grid,
    Hearts,
    Oval,
    Puff,
    Rings,
    SpinningCircles,
    TailSpin,
    ThreeDots,
  } from '@agney/react-loading';
import { BarsSpinner,ClapSpinner,StageSpinner ,MagicSpinner} from "react-spinners-kit";
import axios from 'axios';
import {toast} from 'react-toastify';
const Allcourse = () => {
    

    const [loading,setLoading] = useState(false);


    //functuion to call server
    const getAllCoursesFromServer = () => {
        axios.get(`/courses`).then(
            (response) => {
                console.log(response.data);
                console.log("asdasd");
                toast.success("courses have been loaded",{
                    position: 'bottom-center'
                });
                setCourses(response.data);
                setLoading(true);
            },
            (error) => {
                console.log(error);
                toast.error("error");
                setLoading(true);
            }
        );
    };

    //loading course function
    useEffect(() => {
        document.title = "All Courses";
        getAllCoursesFromServer();
    },[]);

    const [courses,setCourses] = useState([]);


    const updateCourses = (id) => {
        setCourses(courses.filter((c) => c.id !== id));
    }

    return(
        <div>
            {loading ? <div></div> :<div className="centered"> 
                <BeatLoader />    
                </div>
                }
            
            <h1>All Courses</h1>
            <p>List of courses are as Follows</p>
            <div style={{padding:10,fontFamily:'verdana'}}>
                {
                    courses.length>0? courses.map((item)=>
                    
                    <Course key={item.id} course={item} 
                    update={updateCourses} />
                    
                    ) : "No courses"
                }
            </div>
        </div>
    );
}

export default Allcourse;